import { createStore } from "vuex";

export default createStore({
  name: "store",
  state() {
    return {
      counter: 5,
    };
  },
  getters: {},
  mutations: {
    delCounter() {
      this.state.counter = 0;
    },
    addCounter(state, value) {
      this.state.counter = this.state.counter + value;
    },
  },
  actions: {},
  modules: {},
});
