<template>
  <div class="box">
    <h1>{{ msg }}</h1>
    <button type="button" @click="addCounter">{{ opp }} {{ value }}</button>
  </div>
</template>

<script>
export default {
  name: "ComponentBeta",
  data() {
    return {
      msg: "Component   Beta",
      opp: "+",
      value: 5,
    };
  },
  methods: {
    addCounter() {
      this.$store.commit("addCounter", this.value);
    },
  },
};
</script>

