import { createRouter, createWebHistory } from "vue-router";
import ComponentAlpha from "../components/ComponentAlpha.vue";
import ComponentBeta from "../components/ComponentBeta.vue";
import ComponentGamma from "../components/ComponentGamma.vue";

const routes = [
  {
    path: "/ComponentAlpha",
    name: "ComponentAlpha",
    component: ComponentAlpha,
  },
  {
    path: "/ComponentBeta",
    name: "ComponentBeta",
    component: ComponentBeta,
  },
  {
    path: "/ComponentGamma",
    name: "ComponentGamma",
    component: ComponentGamma,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
